footer
{
	display: flex;
	justify-content: space-around;
	margin-top: 30px;
	background-color: #F2F2F2;
	width: 100%;
}

footer ul
{
	list-style-type: none;
	padding-left: 0;
}

footer li
{
	line-height: 2em;
}

footer li:hover
{
	font-weight: bold;
}

@media screen and (max-width: 767px)
{
	footer
	{
		flex-direction: column;
	}

	footer div
	{
		margin-left: 30px;
	}
}